<template>
  <div class="center">
    <div class="blocks">
      <div v-for="i in 10" :key="i" class="col-12">
        <div class="row">
          <div v-for="j in 10" :key="j" class="block" @click="click(((i-1)*10)+j)" :style="getStyle(((i-1)*10)+j)">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    datas: {
      type: Array,
      required: true
    },
    colors: {
      type: Array,
      required: false
    },
    labels:{
      type:Array,
      required:false
    }
  },
  computed: {
    total() {
      if (this.datas) {
        return this.datas.reduce((p, c) => {
          return p + c;
        }, 0);
      }
      return 0;
    },
    finalColors() {
      if (this.colors) {
        return this.colors;
      }
      return this.$store.getters.getColors;
    }
  },
  methods: {
    getStyle(index) {
      let color = {
        backgroundColor: this.finalColors[0]
      };
      this.datas.reduce((p, c, i) => {
        if (this.toPercentage(p + c, this.total) - index < 0) {
          color = {
            backgroundColor: this.finalColors[(i + 1) % this.finalColors.length]
          };
        }
        return p + c;
      }, 0);
      return color;
    },
    indexToData(index) {
      let idx = 0;
      this.datas.reduce((p, c, i) => {
        if (this.toPercentage(p + c, this.total) - index < 0) {
          idx = i + 1;
        }
        return p + c;
      }, 0);
      return this.datas[idx];
    },
    toPercentage(value, total) {
      return value / total * 100;
    },
    click(i) {
      this.$emit('click',this.indexToData(i));
    }
  }
};
</script>

<style>
.center {
  text-align: center;
}
.blocks {
  margin: auto;
  width: 520px;
  height: 520px;
}
.block {
  border-radius: 5px;
  margin: 2px 2px 2px 2px;
  height: 48px;
  width: 48px;
}
.block:hover {
  transform: scale(1.1);
  transition: transform 50ms ease-in;
}
@media screen and (max-width: 786px) {
  .blocks {
    width: 340px;
    height: 340px;
  }
  .block {
    height: 30px;
    width: 30px;
  }
}
@media screen and (max-width: 578px) {
  .blocks {
    width: 280px;
    height: 280px;
  }
  .block {
    height: 24px;
    width: 24px;
  }
}
</style>



